<template>
  <div class="ios-s wrap">
    <app-header title="设置数据走向" showBack="true"></app-header>
    <div class="mian">
      <div class="label-wrap">
          <mt-checklist
            title="设置以下数据发送到DMS"
            v-model="selectedLables"
            :options="labels">
          </mt-checklist>
          <mt-button size="large"  type="primary" @click="submit" style="margin-top:1rem;">完成</mt-button>
      </div>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../components/header/header'
import {sendToDms, getFromDmsLabels, getCouponLabels} from '../../api/maintain'
export default {
  name: 'Dealer-Data',
  data () {
    return {
      labels: [],
      value: '',
      selectedLables: []
    }
  },
  created () {
    this.getLabels()
    getFromDmsLabels()
      .then(data => {
        let datas = data.data.info
        this.selectedLables.push(...datas)
      })
  },
  mounted () {
    this.$on('selectedLabel', (labelID, state) => {
      if (state) {
        this.selectedLables.push(labelID)
      } else {
        this.selectedLables.forEach((item, index) => {
          if (item === labelID) {
            this.selectedLables.splice(index, 1)
          }
        })
      }
    })
  },
  methods: {
    getLabels () {
      getCouponLabels()
        .then(data => {
          let datas = data.data.rank
          for (let i in datas) {
            if (datas[i].labels) {
              for (let j in datas[i].labels) {
                let _obj = {
                  label: datas[i].labels[j].label_cn,
                  value: datas[i].labels[j].label_id
                }
                this.labels.push(_obj)
              }
            }
          }
        })
    },
    submit () {
      sendToDms(this.selectedLables.toString())
        .then(data => {
          if (data.state) {
            this.$toast(data.errormsg || '设置成功')
          } else {
            this.$toast(data.errormsg || '设置失败')
          }
        })
        .catch(err => {
          this.$toast('网络错误，请重试')
          console.log(err)
        })
    }
  },
  components: {AppHeader}
}
</script>

<style lang="less" scoped>
.wrap{
  padding: 55px 0;
  padding-bottom: 300px;
  background: #fff;
  height: 100%;
  .mian {
    .label-wrap {
      h3 {
        margin: 0;
        text-align: center;
      }
    }
  }
}
</style>



